import bus from "@/bus.js";
import { mapState, mapGetters, mapMutations } from 'vuex'; //引入state，getters，mutations
// import axios from 'axios'

export default {
  name: 'myMaterial',
  props: {
    authorId: ''
  },
  data: function data() {
    return {
      loading: false,
      loading_count: 0,
      picData: {
        upload_file_once: {}
      },
      material_own_origin: [],
      material_own_cutout: []
    };
  },
  created: function created() {},
  mounted: function mounted() {// this.busEvent()
  },
  computed: {
    material_own: function material_own() {
      this.material_own_origin.map(function (item) {
        item.check_status = 1;
      });
      this.material_own_cutout.map(function (item) {
        item.check_status = 2;
      });
      return [].concat(_toConsumableArray(this.material_own_origin), _toConsumableArray(this.material_own_cutout));
    }
  },
  methods: {
    cout_load_img: function cout_load_img() {
      // 全部load完解除loading
      this.loading_count += 1;

      if (this.loading_count >= this.material_own.length / 2) {
        this.loading = false;
      }
    },
    updatePage: function updatePage(data) {
      //上传更新页面
      var me = this;
      me.material_own_origin.unshift({
        author: me.authorId,
        check_status: 1,
        id: data.data.file_id,
        path: data.data.savepath
      });
      me.$message('上传成功！');
    },
    beforeUpload: function beforeUpload(file) {
      var me = this; // 上传前

      var isFormat = file.type === 'image/jpeg' || file.type === 'image/png';
      var isLt2M = file.size / 1024 / 1024 < 2;
      me.$set(me.picData, 'upload_file_once', file);

      if (!isFormat) {
        this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
      }

      if (isFormat && !isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }

      return isFormat && isLt2M;
    },
    uploadSuccess: function uploadSuccess(res, file) {
      // 本地上传
      // 上传成功
      this.updatePage(res);
    },
    uploadFail: function uploadFail(err, file, fileList) {
      // 上传失败
      this.$message(err);
    },
    deleteFunc: function deleteFunc(status, id, index) {
      var me = this;
      me.axios.get("/User_images/del_images_self?check_status=".concat(status, "&id=").concat(id)).then(function (data) {
        var origin_len = me.material_own_origin.length;

        if (status == 1) {
          me.material_own_origin.splice(index, 1);
        } else {
          me.material_own_cutout.splice(index - origin_len, 1);
        }

        me.$message('删除成功！');
      }).catch(function (e) {});
    }
  },
  beforeRouteEnter: function beforeRouteEnter(to, from, next) {
    next(function (vm) {
      vm.axios.get("/User_images/find_images_self").then(function (data) {
        vm.material_own_origin = data.data.dataImage;
        vm.material_own_cutout = data.data.dataCutout;

        if (data.data.dataImage.length != 0 || data.data.dataCutout.length != 0) {
          vm.loading = true;
        } else {
          vm.loading = false;
        }
      }).catch(function (e) {});
    });
  }
};